header.topbar {
  position: fixed;
  top: 0;
  left: var(--topbar-left);
  right: 0;
  height: var(--topbar-height);
  background-color: var(--topbar-bg-color);
  border-bottom: 1px solid var(--topbar-border-color);
  display: flex;
  align-items: center;
  padding: var(--topbar-padding);
  box-sizing: border-box;
  z-index: var(--topbar-z-index);
}

/* ПК-версія: пошук */
@media (min-width: 768px) {
  .desktop-search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 600px; 
    margin: 0 auto;  
    width: 100%;    
    position: relative;
  }

  .desktop-search-form input[type="search"] {
    width: 100%;
    padding: var(--search-input-padding);
    font-size: var(--search-input-font-size);
    border: 1px solid var(--search-input-border-color);
    border-radius: var(--search-input-border-radius);
    outline: none;
    box-sizing: border-box;
  }

  .desktop-search-form button {
    position: absolute;
    top: 50%;
    right: var(--search-button-right);
    transform: translateY(-50%);
    background: transparent;
    border: none;
    font-size: var(--search-button-font-size);
    cursor: pointer;
    color: var(--search-button-color);
    padding: var(--search-button-padding);
    line-height: 1;
    transition: var(--search-button-transition);
  }

  .desktop-search-form button:hover {
    color: var(--search-button-hover-color);
  }
}


/* Мобільна версія */
@media (max-width: 767px) {
  .desktop-search-form {
    display: none;
  }
}
